.register-conatiner{
    width:fit-content;
    margin: auto;
    margin-top: 4rem;
    margin-bottom: 1.5rem;
}
.left-part{
    color: red;
    height: 26rem;
}
.card-heading{
   
  animation: blinker 1s linear infinite;
}

@keyframes blinker {
  50% {
    opacity: 0;
  }
}

.side-img{
    margin-top: 4rem;
    width:15rem;
}
.right-part{
    background-color: rgb(89, 165, 216);
    color: rgb(255, 255, 255);
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}
.right-part-title{
    background-color: rgb(89, 165, 216);
}
.form-container{
    display: flex;
    flex-direction: column;
}
.inputBox{
    width:15rem;
    margin: auto;
    /* margin-top: 2.1rem; */
    margin-bottom: 1.9rem;
    border: none;
    outline: none;
    height: 2.1rem;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    
}
.inputBox::placeholder{
    text-align: center;
    color: black;
}
label{
    color: rgb(172 28 70);
    font-size: 1.3rem;
}
.registerSubmit{
    background-color: rgb(11 10 24);
    margin: auto;
    margin-bottom: 1.9rem;
    width: 10rem;
    height: 2rem;
    color: rgb(255, 255, 255);
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

